<template>
  <div class="mineinfo-page">
    <navigation-bar
      class="mineinfo-nav-bar"
      :border="false"
      pageName="账号信息"
      @onLeftClick="onClickLeft"
    />

    <div class="mineinfo-content">
      <div class="content-cell-head" @click="clickedHeaderSet">
        <div class="mine-header-img">
          <img :src="showUserImg" alt />
        </div>
        <div class="cell-header-title">头像设置</div>
        <div class="cell-next-icon">
          <img :src="arrowIcon" alt />
        </div>
      </div>

      <div class="content-cell">
        <van-field
          v-model="userInfo.userAccount"
          label="账户"
          placeholder="账号"
          size="14"
          label-width="60"
          disabled
        />
      </div>

      <div class="content-cell">
        <van-field v-model="userInfo.userNickname" label="昵称" placeholder="昵称" size="14" label-width="60" maxlength="16" />
      </div>

      <div class="content-cell">
        <van-field v-model="userInfo.userPhoneNum" label="手机" placeholder="手机" size="14" label-width="60" />
      </div>

      <div class="content-push-cell">
        <p>发帖极光推送</p>
        <van-switch class="push-switch" v-model="pushSwitchCtl" size="25" />
      </div>

      <div class="saveButton" @click="onClickSave">保存</div>
    </div>

    <van-action-sheet
      v-model="showPhotoSelectSheet"
      :actions="actions"
      cancel-text="取消"
      round
      close-on-click-action
      @select="onClickedGetPhoto"
    />
  </div>
</template>

<script>

  import {
    getUserInfoService,
    updateUserInfoService,
  } from "../../services/mineService";
  import {store, storage} from "../../utils/store";
  import config from "../../common/config";

  import Vue from "vue";
  import { NavBar, Field, Switch, Toast, ActionSheet } from "vant";
  Vue.use(NavBar).use(Field).use(Switch).use(ActionSheet);

  // 头像
  import userImg from "@/assets/imgs/mine/mine_duck.png";
  // 箭头icon
  import arrowIcon from "@/assets/imgs/mine/mine_arrow.png";
  import {updateCustNews} from "../../services/loginService";
  import NavigationBar from "../title/NavigationBar";

  export default {
    name: "mineInfoPage",
    components: { NavigationBar },
    created () {
      this.loadPageData();
    },
    data() {
      return {
        active: 0,
        arrowIcon: arrowIcon,
        userInfo: {
          userIcon: '',
          userAccount: '',
          userNickname: '',
          userPhoneNum: '',
          uuid: '',
          WJDM: ''
        },
        pushSwitchCtl: false,
        actions: [
          {name: "拍照", color: '#007aff'},
          {name: "从手机相册选择", color: '#007aff'}
        ],
        showPhotoSelectSheet: false,
      };
    },
    computed: {
      showUserImg: function() {
        return this.userInfo.userIcon != '' ? this.userInfo.userIcon : userImg;
      }
    },
    methods: {
      // 点击头像获取方式
      onClickedGetPhoto(item, index) {
        this.getPictureData(index);
      },
      // 通过摄像头或者本地相册进行图片上传
      getPictureData(selectIndex) {
        selectIndex
          ? this.getLocalPicture()
          : this.useCamera();
      },
      // 点击头像设置
      clickedHeaderSet: function() {
        this.showPhotoSelectSheet = true;
      },
      // 通过摄像头获取
      useCamera() {
        this.$plugin.usePlugin("camera")(
          imageData => {
            this.uploadHeadPhotoFile(imageData);
          },
          error => {
            Toast.fail(error);
          }
        );
      },
      // 通过相册获取
      getLocalPicture() {
        this.$plugin.usePlugin("getPictures")(
          1,
          images => {
            images.forEach(imageData => {
              this.uploadHeadPhotoFile(imageData);
              //this.selectPictures.push(`data:image/jpeg;base64,${imageData}`);
            });
          },
          error => {
            Toast.fail(error);
          }
        );
      },
      // 上传图片数据
      uploadHeadPhotoFile(fileURL) {
        let loading = Toast.loading({
          duration: 0,
          mask: true,
          message: "上传中..."
        });
        let reqUrl = `${store.get('custUrl.homeUrl')}xwj-sys-media/media/upload/uploadFile`;

        var options = new FileUploadOptions();
        var params = {};
        params.file = fileURL;
        params.source = "wyApp";
        params.business = "userPic";// 用户头像
        options.params = params;
        var headers = { contentType: "multipart/form-data" };
        options.headers = headers;
        this.$plugin.usePlugin("fileTransfer")(
          reqUrl,
          fileURL,
          result => {
            loading.clear();
            let resData = JSON.parse(result.response);
            if (resData.resultCode == 0) {
              this.userInfo.WJDM = resData.data;
              this.userInfo.userIcon = resData.data;
            } else {
              console.log('resData: ' + resData);
              Toast.fail(resData.msg);
            }
          },
          error => {
            loading.clear();
            console.log('fail error: ' + error);
            Toast.fail(error);
          },
          options
        );
      },

      // 获取当前页面数据
      loadPageData: function() {
        let custNews = store.get('custNews');
        custNews = custNews ? custNews: storage.getStorage("custNews");
        let userData = {
            AREA_CODE: custNews.AREA_CODE,
            USER_CODE: custNews.USER_CODE,
        };
        getUserInfoService(userData)
        .then(noteData => {
          let userPic = noteData.PIC_URL;
          if (userPic != '') {
              this.userInfo.userIcon = userPic;
          }
          this.userInfo.userAccount = noteData.USER_ACCNT;
          this.userInfo.userNickname = noteData.USER_NICK_NAME;
          this.userInfo.userPhoneNum = noteData.PHONE;
          this.userInfo.uuid = noteData.UUID;
          this.pushSwitchCtl = noteData.IS_NOTE_PUSH ? true : false;
        })
        .catch(error => {
            Toast.fail(error);
        });
      },
      // 保存数据
      savePageData: function() {
        let custNews = store.get('custNews');
        custNews = custNews ? custNews: storage.getStorage("custNews");
        let userInfo = {
          PIC_URL: this.userInfo.userIcon,
          USER_CODE: custNews.USER_CODE,
          USER_NICK_NAME: this.userInfo.userNickname,
          UUID: this.userInfo.uuid,
          PHONE: this.userInfo.userPhoneNum,
          IS_NOTE_PUSH: this.pushSwitchCtl ? '1' : '0',
          WJDM: this.userInfo.WJDM
        };
        updateUserInfoService(userInfo)
        .then(noteData => {
          Toast(noteData.msg);
          store.get("updatePageData")(userInfo);
          store.get("updateFindUserInfo")(userInfo);
          custNews.PIC_URL = this.userInfo.userIcon;
          custNews.USER_NICK_NAME = this.userInfo.userNickname;
          custNews.UUID = this.userInfo.uuid;
          custNews.PHONE = this.userInfo.userPhoneNum;
          custNews.WJDM = this.userInfo.WJDM;
          updateCustNews(custNews);
          setTimeout(() => {
            if (noteData.success) {
              this.onClickLeft();
            }
          }, 600);
        })
        .catch(error => {
            Toast.fail(error);
        });
      },
      onClickLeft() {
        this.$router.go(-1);
      },
      onClickSave() {
        let reg = "^[\u4E00-\u9FA5A-Za-z0-9_]+$";
        if("" == this.userInfo.userNickname){
          Toast("用户昵称不能为空");
        }
        else if(!this.userInfo.userNickname.match(reg)){
          Toast("用户昵称仅支持汉字、字母、数字组合");
        }
        else if(this.userInfo.userNickname.length >= 40){
          Toast("用户昵称过长","center");
        }
        else if("" == this.userInfo.userPhoneNum){
          Toast("号码不能为空");
        }
        else if(11 != this.userInfo.userPhoneNum.length){
          Toast("手机号必须为11位");
        }
        else if(!(/^(((11[0-9]{1})|(12[0-9]{1})|(13[0-9]{1})|(14[0-9]{1})|(15[0-9]{1})|(16[0-9]{1})|(17[0-9]{1})|(18[0-9]{1})|(19[0-9]{1}))+\d{8})$/.test(this.userInfo.userPhoneNum))){
          Toast("手机号格式不正确");
        }
        else{
          this.savePageData();
        }
      }
    }
  };
  </script>

